export default {
    number: {
        replace: (num) => {
            switch (true) {
                case isNaN(num):
                    return { $t: "number", v: "NaN" };
                case num === Infinity:
                    return { $t: "number", v: "Infinity" };
                case num === -Infinity:
                    return { $t: "number", v: "-Infinity" };
                default:
                    return num;
            }
        },
        revive: ({ v }) => Number(v),
    },
};
