export const BisonBinaryTypes = {
    Blob: {
        test: (blob, toStringTag) => toStringTag === "Blob",
        replace: (blob, altChannel) => {
            const i = altChannel.length;
            altChannel.push(blob);
            return {
                $t: "Blob",
                mimeType: blob.type,
                i,
            };
        },
        revive: ({ i, mimeType }, altChannel) => new Blob([altChannel[i]], { type: mimeType }),
    },
};
